import React from 'react';
import TeamGraph from './TeamGraph';
import axios from 'axios';
import api from '../helpers/api';
import Airtable from 'airtable';

const base = new Airtable({apiKey: 'keyMMhm1hEx7OtaIC'}).base('apputYC83th3otdfX');

class GraphsContainer extends React.Component {
  constructor (props) {
    super(props);
    this.state = {
      projects: []
    }
    this.loadProjects = this.loadProjects.bind(this);
  }

  componentDidMount() {
    this.loadProjects();
  }

  loadProjects () {
    axios.get('https://api.airtable.com/v0/apputYC83th3otdfX/Team%20Tracking?Office%20Submitted=Chicago',  {
      headers: { Authorization: "Bearer "+ "keyMMhm1hEx7OtaIC" }
    }).then(res => {
      console.log(res.data.records);
      this.setState({projects: res.data.records});
    }).catch(err => {
      console.log(err);
    })
  }

  render() {
    return (
      <div>
        {!this.state.projects
         ? <div className="graphs-container">
            <p>Loading</p>
           </div>  
         : <div className="graphs-container">
              <h1>Count: {this.state.projects.length}</h1>
          </div>}
      </div>
    )
  }
};

module.exports = GraphsContainer;